var render = function render(){var _vm=this,_c=_vm._self._c;return _c('div',{attrs:{"id":"Werchterfestival"}},[_vm._m(0),_c('div',{attrs:{"id":"text"}},[_c('b-text',[_c('br'),_c('p',[_vm._v(" Rock Werchter es un festival de música anual que se lleva a cabo en el pueblo de Werchter , cerca de Lovaina , Bélgica, desde 1976 y es un festival de música rock de gran tamaño . Los festivales de 2003, 2005, 2006, 2007, 2012 y 2014 recibieron el premio Arthur al mejor festival del mundo en la International Live Music Conference (ILMC). Puede albergar 88.000 invitados diarios, de los cuales 67.500 combinan los cuatro días, para sumar un total máximo de 149.500 asistentes diferentes. ")]),_c('p',[_vm._v(" El festival comenzó en 1974 como un evento de un día con actuaciones de Banzai y Kandahar, pero con los años ha evolucionado hasta convertirse en uno de los festivales de música más importantes de Bélgica. Originalmente era un festival doble, llamado \"Torhout-Werchter\", con dos áreas de festival en diferentes lugares de Bélgica: una en Werchter y otra en Torhout . En 1999, el festival abandonó el sitio de Torhout y desde entonces se lleva a cabo solo en Werchter. Desde 2003, Werchter ha sido un festival de 4 días, ya que fue vendido por el propietario Herman Schueremans a los organizadores estadounidenses Live Nation . Sin embargo, Schueremans sigue siendo el principal organizador del evento. El festival se organiza cada primer fin de semana de las vacaciones de verano en Bélgica (último fin de semana de junio o primero de julio). ")]),_c('p',[_vm._v(" En los últimos años, ha habido controversia sobre el aumento de los precios de las entradas. Debido a esto, Schueremans fue interrumpido durante una aparición en Pop Poll de HUMO , un programa de premios alternativo belga. A 200 € por cuatro días (18 € adicionales para incluir el campamento o 25 € para acampar xl) en 2012, todavía se consideraba un festival de música relativamente económico. ")]),_c('p',[_vm._v(" Los sitios para acampar abren oficialmente a las 8 am el primer día del festival, pero debido a que los invitados llegan temprano y acampan en la calle, los organizadores tradicionalmente se han visto obligados a abrir los sitios para acampar temprano, a veces más de 24 horas. Los campamentos están ubicados a lo largo de las tres principales vías de entrada a Werchter (desde Haacht , Aarschot y Lovaina ), la mayoría de ellos dentro de un kilómetro del lugar del festival, pero algunos ubicados hasta a 3 kilómetros de distancia. En 2011, por primera vez, los boletos de campamento XL están disponibles, lo que permite a los asistentes al festival llegar y acampar a partir de las 4:00 p. m. del miércoles anterior al festival. ")]),_c('p',[_vm._v(" Hasta la década de 1990, el festival atraía principalmente a asistentes flamencos , pero en los últimos años se ha vuelto cada vez más internacional con una afluencia especialmente grande de visitantes holandeses , franceses , valones y británicos , con notables contingentes australianos , sudafricanos y otros. [5] Los belgas van alternativamente a los festivales Pukkelpop y Dour . ")]),_c('p',[_vm._v(" Todos los gráficos de Rock Werchter, incluidos los carteles, fueron diseñados por Tom Hautekiet , quien murió en abril de 2020. ")]),_c('br')])],1),_vm._m(1)])
